.nav {


	&__logo a{
		border: none;

		&:hover {
			border: none;
		}
	}

	& svg.logo{
	 margin: .5rem 0; 
	}

	&__menu {

		&__item ul {
			margin: 0;
			padding: 0;
		}
	}
}

article {
	color: #000;
}


.accent, .accent a {
	color: @accent;
}

.bg {
	background-color: #fff;
}


.image {

	&__overlay {
		overflow: hidden;
		height: 70%;
	}


}

.easter-egg {
	position: absolute;
	right: 0;
	top: 0;
	margin: 1rem 0;
	background-image: url(../../img/svg/easter-egg.svg);
	width: 10px;
	height: 13px;
	cursor: pointer;
	transition: 500ms cubic-bezier(0, 0, 0.2, 1);

	&:hover {
		background-image: url(../../img/svg/easter-egg-hover.svg);
		transition: 500ms cubic-bezier(0, 0, 0.2, 1);
	}
}


@media (max-width: 800px) {

	.news {
		&__item:nth-child(n+4) {
			display: none;
		}
	}

}










